.App {
  margin: 3em auto;
  width: 90%;
}

/* FontAwesome Heart */

.fa-heart {
  color: #a94442;
  font-size: 12px;
  position: absolute;
  right: 0;
  top: 0;
}

.fa-heart:hover {
  color: #ccc;
}

.fa-heart-o {
  font-size: 12px;
  position: absolute;
  right: 0;
  top: 0;
}

.fa-heart-o:hover {
  color: #a94442;
}
/********************/

.gif-item {
  box-shadow: 2px 2px 4px 0 #ccc;
  box-sizing: border-box;
  display: inline-block;
  margin: 0 0 1.5em;
  padding: 1em;
  width: 100%;
  position: relative;
}

.gif-item img{
  width: 100%;
}

.gif-list {
  grid-column-gap: 1.5em;
  -webkit-column-gap: 1.5em;
          column-gap: 1.5em;
  font-size: .85em;
  margin: 1.5em 0;
  padding: 0;
}

.gif-modal {
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

input {
  display: block;
  font-size: 22px;
  height: 50px;
  margin: 0 auto;
  text-align: center;
  width: 50%;
}

@media only screen and (min-width: 400px) {
  .gif-list {
    -webkit-column-count: 2;
    column-count: 2;
  }
}

@media only screen and (min-width: 700px) {
  .gif-list {
    -moz-column-count: 3;
    -webkit-column-count: 3;
    column: 3;
  }
}

@media only screen and (min-width: 900px) {
  .gif-list {
    -moz-column-count: 4;
    -webkit-column-count: 4;
    column: 4;
  }
}

@media only screen and (min-width: 1100px) {
  .gif-list {
    -moz-column-count: 5;
    -webkit-column-count: 5;
    column: 5;
  }
}

@media only screen and (min-width: 1280px) {
  .App {
      width: 1140px;
  }
}



